* {
    border: 0;
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(3, 5, 34);
}

header {
    margin: 0 15px;
    h1 {
        text-align: center;
        font-family: 'Archivo Black';
        font-size: 40px;
        color: white;
        margin-top: 20px;
        font-weight: 100;
    }

    h2 {
        text-align: center;
        font-family: 'Lobster';
        font-size: 35px;
        font-weight: 100;
        letter-spacing: 2px;
        color: white;
        font-style: italic;
        margin-top: 20px;
    }
}

main {
    width: 300px;
    height: 300px;
    border: 1px solid white;
    margin: auto;
    margin-top: 30px;
    border-radius: 12px;
    padding: 15px;
    box-sizing: border-box;

    & > p.name {
        color: white;
        font-size: 20px;
        font-family: Montserrat;
        margin-bottom: 10px;
    }

    & > input.display {
        color: white;
        text-align: center;
        font-size: 25px;
        background-color: rgba(240, 248, 255, 0.073);
        padding: 8px 0;
        border-radius: 5px;
        font-family: Montserrat;
        width: 100%;
    }

    & > label > p {
        color: white;
        font-size: 20px;
        font-family: Montserrat;
        margin-top: 20px;
        margin-bottom: 10px;
    }
    
    & > label > input {
        color: white;
        text-align: center;
        font-size: 25px;
        background-color: rgba(240, 248, 255, 0.073);
        padding: 8px 0;
        border-radius: 5px;
        font-family: Montserrat;
        width: 100%;
        margin-bottom: 20px;

    }

    button {
        display: block;
        margin: auto;
        padding: 3px 8px;
        border-radius: 8px;
        font-family: Montserrat;
        font-size: 25px;
        font-weight: 700;
        border: 1px solid rgba(255, 255, 255, 0);
    }

    button:hover {
        background-color: rgb(23, 20, 42);
        color: aliceblue;
        cursor: pointer;
        border: 1px solid white;
    }


}